.node {
  cursor: move;
  stroke-width: 1.5px;
  transition: filter 0.3s ease;
}
.link {
  fill: none;
  stroke: #bdbdbd;
  stroke-width: 2px;
  opacity: 0.4;
  transition: filter 0.3s ease;
  marker-end: url(#end-arrow);
}
.node:hover,
.link:hover {
  filter: brightness(0.6);
}
.label {
  fill: #cccccc;
  font-size: 9px;
  text-anchor: middle;
  cursor: move;
  font-family: inherit;
}
#end-arrow {
  opacity: 0.4;
}
.INPUT {
  fill: #ff6666;
  stroke: #ff4d4d;
}
